





































import Vue from "vue";
import KLoading from "../../../components/KLoading.vue";
import KDialog from "../../../components/KDialog.vue";
import KDataList from "../../../components/KDataList.vue";

import { Display } from "../../../configuration/display";

import { Header, HeaderIcon, HeaderAction } from "../../../models/header";
import { Headers } from "../../../models/headers";

import {
  IProdutosListaView,
  IProdutosListaController
} from "../../../contracts/Cadastros/ProdutosListaContract";

import { ProdutosListaController } from "../../../controllers/Cadastros/ProdutosListaController";

import Component from "vue-class-component";

@Component({
  components: {
    KLoading,
    KDialog,
    KDataList
  }
})
export default class ProdutosLista extends Vue implements IProdutosListaView {
  _controller?: IProdutosListaController;

  dialogLoading = false;
  dialogMessage = false;
  textError = "";

  loading(visible: boolean): void {
    this.dialogLoading = visible;
  }
  showMsg(msg: string): void {
    this.textError = msg;
    this.dialogMessage = true;
  }
  showItens(list: any): void {
    this.itens = list;
  }
  fecharDialogMessage() {
    this.dialogMessage = false;
  }

  isMobile = false;

  headers = new Headers([
    new Header("Código", "codigo", false, false),
    new Header("Produto", "nome", false, false),
    new Header("Grupo", "grupo", false, false),
    new Header("Item de Compra", "itemCompra", false, false)
  ]);

  itens = [];

  importLista() {
    this._controller!.importItens();
  }

  created() {
    this.isMobile = Display.isMobile();
    this._controller = new ProdutosListaController(this);

    this._controller.carregarItens();
  }
}
