import { IProdutosListaView, IProdutosListaController } from "../../contracts/Cadastros/ProdutosListaContract";
import axios from "../../plugins/axios"

export class ProdutosListaController implements IProdutosListaController {


    _view: IProdutosListaView

    constructor(view: IProdutosListaView) {
        this._view = view
    }

    importItens(): void {
        this._view.loading(true)

        axios.get('Produtos/Integracao').then((response: any) => {
            this._view.loading(false)
            if (response.data.sucesso) {
                //this._view.showMsg("Importação completa.")
                this.carregarItens()
            } else {
                this._view.showMsg(response.data.message)
            }
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e)
        })
    }

    carregarItens(): void {
        this._view.loading(true)

        axios.get('Produtos').then((response: any) => {
            this._view.loading(false)
            this._view.showItens(response.data)
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e)
        })
    }

}